.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.superRoot {
  position: relative;
}
#root {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 1000;
  color:white;
  width: 100%;
}

.NavBarTop {
  min-height:30px !important;
}
.navbar{
  border-radius: 0;
}
.myNavTop.navbar-nav li a {
  padding:0 10px;
  line-height:30px;
}
.myNavBottom.navbar-nav li a {
  padding:0 15px;
  line-height:30px;
}
.myNavItemBottomOthers a {
  padding:0 15px;
  font-size:18px;
  margin-top:15px;
}


